import { useEffect, useState } from "react";
import { getDatabase, set, ref } from "firebase/database";
import { useNavigate } from "react-router-dom";
import { Tooltip } from "react-tooltip";
import "react-tooltip/dist/react-tooltip.css";
const tooltips = {
  Понеділок: {
    first: "Суп гречаний з куркою, хліб пшеничний",
    second:
      'Салат "Марсель" з куркою, куряча відбивна з грибами та сиром, каша булгур з вершковим маслом, хліб пшеничний.',
    third: "Комплекс перше + друге 80 грн",
    fourth: "Вареники з картоплею, булочка з крошкою, компот з ягід.",
  },
  Вівторок: {
    first: 'Суп "Харчо", хліб пшеничний',
    second:
      "Капуста пелюстками червона, котлета куряча парова, картопляне пюре з вершковим маслом, хліб пшеничний.",
    third: "Комплекс перше + друге 80 грн",
    fourth: "Сирники зі сметаною, какао з молоком.",
  },
  Середа: {
    first: "Крем-суп грибний, хліб пшеничний",
    second:
      "Салат з буряку та сиру фета, картопля запечена з куркою та овочами, хліб пшеничний.",
    third: "Комплекс перше + друге 80 грн",
    fourth:
      'Оладки з цукіні під сметаново-зеленим соусом, булка "Кокосівна", компот з ягід.',
  },
  Четвер: {
    first: "Суп з фрикадельками, хліб пшеничний",
    second:
      'Салат "Вітамінний" (свіжа капуста, огірок), макарони з вершковим маслом, крученик в беконі, хліб пшеничний.',
    third: "Комплекс перше + друге 80 грн",
    fourth:
      'Драники картопляні зі сметаною, булка "Харківська" зі згущеним молоком, какао з молоком',
  },
  "П’ятниця": {
    first: "Борщ український з куричим м’ясом , хліб пшеничний",
    second:
      'Салат "Літній", каша гречана з вершковим маслом, биток рублений з курки, хліб пшеничний.',
    third: "Комплекс перше + друге 80 грн",
    fourth: 'Вареники з картоплею та печінкою, булка "Бананчик", компот з ягід',
  },
};

export default function Form({
  login,
  send,
  setIsOrderSuccess,
  validatedForms,
  setValidatedForms,
  isValidated
}) {
  //ім'я
  const [name, setName] = useState("");
  //Перше
  const [first, setFirst] = useState(false);
  //Друге
  const [second, setSecond] = useState(false);
  //Комплекс перше + друге
  const [third, setThird] = useState(false);
  //Щоб наїстись
  const [fourth, setFourth] = useState(false);
  //Роял Бургер
  const [addFirst, setAddFirst] = useState(false);
  //Рис з куркою та овочами в соусі "Териякі"
  const [addSecond, setAddSecond] = useState(false);
  //Лапша "Удон" з морепродуктами
  const [addThird, setAddThird] = useState(false);
  //Лапша "Удон" з куркою та овочами
  const [addFourth, setAddFourth] = useState(false);
  //Дата замовлення
  const [orderDate, setOrderDate] = useState("");

  const inputHandler = (e, setValue) => {
    setValue(e.target.value);
  };

  const checkboxHandler = (e, setValue) => {
    setValue(e.currentTarget.checked);
  };

  const navigate = useNavigate();

  const [tooltip, setTooltip] = useState();

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const dayParam = urlParams.get("day"); // отримуємо день тижня з URL

    setTooltip(tooltips[dayParam]);

    const daysOfWeek = [
      "Неділя",
      "Понеділок",
      "Вівторок",
      "Середа",
      "Четвер",
      "П’ятниця",
      "Субота",
    ];
    const today = new Date();
    const todayDayOfWeek = today.getDay(); // отримуємо день тижня сьогодні (0 - Неділя, 1 - Понеділок, і т.д.)

    const dayParamIndex = daysOfWeek.indexOf(dayParam); // отримуємо індекс дня тижня з URL в масиві daysOfWeek

    let daysUntilDayParam;
    if (todayDayOfWeek <= dayParamIndex) {
      // якщо день тижня з URL ще не настав цього тижня
      daysUntilDayParam = dayParamIndex - todayDayOfWeek;
    } else {
      // якщо день тижня з URL вже був цього тижня
      daysUntilDayParam = 7 - todayDayOfWeek + dayParamIndex;
    }

    const dayParamDate = new Date(
      today.getFullYear(),
      today.getMonth(),
      today.getDate() + daysUntilDayParam
    );
    const formattedDate = dayParamDate
      .toLocaleDateString("uk-UA")
      .replace(/\./g, ""); // перетворюємо дату в формат 'Число.Місяць.Рік'

    setOrderDate(formattedDate);
  });
  useEffect(() => {
    if (send) {
      if (name === "") {
        setMessage("Введіть ім'я");
      } else if (
        !(
          first ||
          second ||
          third ||
          fourth ||
          addFirst ||
          addSecond ||
          addThird ||
          addFourth
        )
      ) {
        setMessage("Виберіть хоча б одну позицію");
      } else {
        setMessage("");
        setValidatedForms([...validatedForms, true]);
      }
    }
  }, [send]);
  useEffect(() => {
    if (send && isValidated) {
      const uniqid = Date.now();
      const db = getDatabase();
      const dataRef = ref(
        db,
        `orders/${orderDate}/${login}/${name + "-" + uniqid}`
      );
      const data = {
        name: {
          data: name,
          text: "Ім'я",
        },
        first: {
          data: first,
          text: "Перше",
        },
        second: {
          data: second,
          text: "Друге",
        },
        third: {
          data: third,
          text: "Комплекс перше + друге",
        },
        fourth: {
          data: fourth,
          text: "Щоб наїстись",
        },
        addFirst: {
          data: addFirst,
          text: "Роял Бургер",
        },
        addSecond: {
          data: addSecond,
          text: 'Рис з куркою та овочами в соусі "Териякі"',
        },
        addThird: {
          data: addThird,
          text: 'Лапша "Удон" з морепродуктами',
        },
        addFourth: {
          data: addFourth,
          text: 'Лапша "Удон" з куркою та овочами',
        },
      };
      set(dataRef, data).then(() => {
        setIsOrderSuccess(true);
        setTimeout(() => {
          setName("");
          setFirst(false);
          setSecond(false);
          setThird(false);
          setFourth(false);
          setAddFirst(false);
          setAddSecond(false);
          setAddThird(false);
          setAddFourth(false);
          navigate("/");
        }, 3000);
      });
    }
  }, [send, isValidated]);

  const [message, setMessage] = useState("");

  return (
    <div className="cart__form p-3 p-lg-5 border border-danger rounded pe-5">
      <div className="row d-flex flex-column flex-sm-row">
        <div className="col">
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="flexCheckDefault"
              value={first}
              data-tooltip-id={"1"}
              data-tooltip-content={tooltip ? tooltip.first : ""}
              onChange={(e) => checkboxHandler(e, setFirst)}
            />
            <Tooltip id={"1"} />
            <label
              className="form-check-label text-nowrap"
              htmlFor="flexCheckDefault"
            >
              Перше
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="flexCheckDefault"
              value={second}
              data-tooltip-id={"2"}
              data-tooltip-content={tooltip ? tooltip.second : ""}
              onChange={(e) => checkboxHandler(e, setSecond)}
            />
            <Tooltip id={"2"} />
            <label
              className="form-check-label text-nowrap"
              htmlFor="flexCheckDefault"
            >
              Друге
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="flexCheckDefault"
              value={third}
              data-tooltip-id={"3"}
              data-tooltip-content={tooltip ? tooltip.third : ""}
              onChange={(e) => checkboxHandler(e, setThird)}
            />
            <Tooltip id={"3"} />
            <label
              className="form-check-label text-nowrap"
              htmlFor="flexCheckDefault"
            >
              Комплекс перше + друге
            </label>
          </div>
          <div className="form-check">
            <input
              className="form-check-input"
              type="checkbox"
              id="flexCheckDefault"
              value={fourth}
              data-tooltip-id={"4"}
              data-tooltip-content={tooltip ? tooltip.fourth : ""}
              data-bs-toggle="tooltip"
              data-bs-placement="top"
              title={tooltip ? tooltip.fourth : ""}
              onChange={(e) => checkboxHandler(e, setFourth)}
            />
            <Tooltip id={"4"} />
            <label
              className="form-check-label text-nowrap"
              htmlFor="flexCheckDefault"
            >
              Щоб наїстись
            </label>
          </div>
        </div>
        <div className="col">
          <label
            htmlFor="exampleInputEmail1"
            className="form-label text-nowrap"
          >
            Ваше ім'я
          </label>
          <input
            type="text"
            value={name}
            onChange={(e) => inputHandler(e, setName)}
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
          />
          <div className="dropdown m-2">
            <button
              className="btn btn-danger dropdown-toggle"
              data-bs-toggle="dropdown"
              type="button"
            >
              Додатково
            </button>
            <ul className="dropdown-menu ps-2 pe-2 dropdown-menu-start">
              <li>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="flexCheckDefault"
                    value={addFirst}
                    onChange={(e) => checkboxHandler(e, setAddFirst)}
                  />
                  <label
                    className="form-check-label text-nowrap"
                    htmlFor="flexCheckDefault"
                  >
                    Роял Бургер
                  </label>
                </div>
              </li>
              <li>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="flexCheckDefault"
                    value={addSecond}
                    onChange={(e) => checkboxHandler(e, setAddSecond)}
                  />
                  <label
                    className="form-check-label "
                    htmlFor="flexCheckDefault"
                  >
                    Рис з куркою та овочами в соусі "Териякі"
                  </label>
                </div>
              </li>
              <li>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="flexCheckDefault"
                    value={addThird}
                    onChange={(e) => checkboxHandler(e, setAddThird)}
                  />
                  <label
                    className="form-check-label text-nowrap"
                    htmlFor="flexCheckDefault"
                  >
                    Лапша "Удон" з морепродуктами
                  </label>
                </div>
              </li>
              <li>
                <div className="form-check">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    id="flexCheckDefault"
                    value={addFourth}
                    onChange={(e) => checkboxHandler(e, setAddFourth)}
                  />
                  <label
                    className="form-check-label text-nowrap"
                    htmlFor="flexCheckDefault"
                  >
                    Лапша "Удон" з куркою та овочами
                  </label>
                </div>
              </li>
            </ul>
          </div>
          <div className="text-danger fade-in">{message}</div>
        </div>
      </div>
    </div>
  );
}
