import { useEffect } from "react";
import { Header, Footer, Cart} from "../components";
import { useNavigate } from 'react-router-dom';

export default function CartPage({login}) {
  const navigate = useNavigate();

  useEffect(() => {
    if (login.length === 0) {
      navigate("/")
    }
  })

  return (
    <>
      <Header />
      <Cart login={login}/>
      <Footer />
    </>
  );
}
