import { Header, Modal, Hero, Menu, About, Footer } from "../components";

export default function HomePage({login, setLogin}) {
  
  return (
    <>
      <Header login={login}/>
      <Modal setLogin={setLogin}/>
      <Hero />
      <Menu setLogin={setLogin} login={login}/>
      <About/>
      <Footer/>
    </>
  );
}
