import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';
import { saveAs } from 'file-saver';
import { getDatabase, ref, get } from 'firebase/database';

const AdminPage = (props) => {
  const [orders, setOrders] = useState({});

  const today = new Date();
  let tomorrow = new Date(today);
  tomorrow.setDate(today.getDate() + 1);
  const formattedDate = tomorrow.toLocaleDateString('en-GB').replace(/[^\d]/g, '');

  const path = "orders/" + formattedDate;

  const database = getDatabase();

  const dataRef = ref(database, path);

  useEffect(() => {
    get(dataRef)
    .then((snapshot) => {
      if (snapshot.exists()) {
        const jsonData = snapshot.val();
        setOrders(jsonData)
      } else {
        console.log('No data available');
      }
    })
    .catch((error) => {
      console.error('Error fetching data:', error);
    });
  }, [])

  const downloadOrders = () => {
    const data = [];

    Object.entries(orders).forEach(([date, logins]) => 
      Object.entries(logins).forEach(([login, names]) => 
        Object.entries(names).forEach(([name, order]) => {
          data.push({
            ["Логін"]: login,
            ["Ім'я"]: order.name.data,
            ["Перше"]: order.first.data ? 'Так' : 'Ні',
            ["Друге"]: order.second.data ? 'Так' : 'Ні',
            ["Комплекс перше + друге"]: order.third.data ? 'Так' : 'Ні',
            ["Роял Бургер"]: order.addFirst.data ? 'Так' : 'Ні',
            ["Рис з куркою та овочами в соусі \"Териякі\""]: order.addSecond.data ? 'Так' : 'Ні',
            ["Лапша \"Удон\" з морепродуктами"]: order.addThird.data ? 'Так' : 'Ні',
            ["Лапша \"Удон\" з куркою та овочами"]: order.addFourth.data ? 'Так' : 'Ні',
            ["Щоб наїстись"]: order.fourth.data ? 'Так' : 'Ні',
          });
        })
      )
    );

    const csv = Papa.unparse(data);
    const blob = new Blob([csv], {type: "text/plain;charset=utf-8"});
    saveAs(blob, "orders.csv");
  }

  return (
    <div className="container">
      <h2 className="my-4">Адміністративна сторінка</h2>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Логін</th>
            <th>Ім'я</th>
            <th>Перше</th>
            <th>Друге</th>
            <th>Комплекс перше + друге</th>
            <th>Роял Бургер</th>
            <th>Рис з куркою та овочами в соусі "Териякі"</th>
            <th>Лапша "Удон" з морепродуктами</th>
            <th>Лапша "Удон" з куркою та овочами</th>
            <th>Щоб наїстись</th>
          </tr>
        </thead>
        <tbody>
          {Object.entries(orders).map(([login, names]) => 
              Object.entries(names).map(([name, order], index) => (
                <tr key={index}>
                  <td>{login}</td>
                  <td>{order.name.data}</td>
                  <td>{order.first.data ? 'Так' : 'Ні'}</td>
                  <td>{order.second.data ? 'Так' : 'Ні'}</td>
                  <td>{order.third.data ? 'Так' : 'Ні'}</td>
                  <td>{order.addFirst.data ? 'Так' : 'Ні'}</td>
                  <td>{order.addSecond.data ? 'Так' : 'Ні'}</td>
                  <td>{order.addThird.data ? 'Так' : 'Ні'}</td>
                  <td>{order.addFourth.data ? 'Так' : 'Ні'}</td>
                  <td>{order.fourth.data ? 'Так' : 'Ні'}</td>
                </tr>
              ))
            )}
        </tbody>
      </table>
      <button className="btn btn-primary" onClick={downloadOrders}>Вивантажити</button>
    </div>
  );
}

export default AdminPage;
