import Form from "./Form";
import plusIcon from "../../assets/icons/plus.svg";
import { useEffect, useState } from "react";

export default function Cart({ login }) {
  const [validatedForms, setValidatedForms] = useState([]);
  const [isValidated, setIsValidated] = useState(false);
  const [send, setSend] = useState(false);
  const [isOrderSuccess, setIsOrderSuccess] = useState(false);
  const [forms, setForms] = useState([<Form />]);

  const onClick = () => {
    setForms([...forms, <Form />]);
  };

  useEffect(() => {
    if (forms.length <= validatedForms.length) {
      setIsValidated(true);

    }
  }, [forms, validatedForms])

  return (
    <section className="cart">
      <div
        className="d-flex flex-column align-items-center justify-content-center mt-5"
        style={{ gap: "20px" }}
      >
        {forms.map((element, i) => (
          <Form
            login={login}
            key={i}
            send={send}
            setIsOrderSuccess={setIsOrderSuccess}
            setValidatedForms={setValidatedForms}
            validatedForms={validatedForms}
            isValidated={isValidated}
          />
        ))}
        <div
          className="cart__add btn btn-danger rounded-pill"
          onClick={onClick}
        >
          <img src={plusIcon} width={30} height={30} alt="plusIcon" />
        </div>
        <div
          className="btn btn-danger mt-3"
          onClick={() => setSend(Date.now())}
        >
          Підтвердити всі замовлення
        </div>
        {isOrderSuccess && (
          <div className="modal show d-block fade-in" tabIndex="-1">
            <div className="modal-dialog modal-dialog-centered">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title">Успіх!</h5>
                  <button
                    type="button"
                    className="btn-close"
                    onClick={() => setIsOrderSuccess(false)}
                  ></button>
                </div>
                <div className="modal-body">
                  <p>Ваше замовлення було успішно відправлено!</p>
                  <p>Ви будете перенаправлені на головну сторінку.</p>
                </div>
                <div className="modal-footer">
                  <button
                    type="button"
                    className="btn btn-danger"
                    onClick={() => setIsOrderSuccess(false)}
                  >
                    Закрити
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </section>
  );
}
