import { NavLink, useLocation } from "react-router-dom";

import logotype from "../../assets/content/logotype.png";
import phoneIcon from "../../assets/icons/phone.svg";
import loginIcon from "../../assets/icons/login.svg";
import locationIcon from "../../assets/icons/location.png";
import arrowRight from "../../assets/icons/arrow-right.svg";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.js";
const dropdownItems = [
  {
    text: "Понеділок",
    path: "cart/?day=Понеділок",
  },
  {
    text: "Вівторок",
    path: "cart/?day=Вівторок",
  },
  {
    text: "Середа",
    path: "cart/?day=Середа",
  },
  {
    text: "Четвер",
    path: "cart/?day=Четвер",
  },
  {
    text: "П'ятниця",
    path: "cart/?day=П'ятниця",
  },
];

export default function Header({ login }) {
  const { pathname } = useLocation();

  return (
    <nav className="navbar navbar-expand-lg bg-light" role="navigation">
      <div className="container">
        <a className="navbar-brand" href="/">
          <img src={logotype} width={89} height={92} alt="Logotype" />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse ms-lg-5" id="navbarNav">
          {pathname === "/cart/" ? (
            <div className="d-flex justify-content-center">
              <NavLink to="/" className="btn btn-danger">
                Назад
                <img
                  src={arrowRight}
                  alt="Arrow"
                  width={25}
                  className="ms-2"
                  height={25}
                  style={{
                    filter:
                      "invert(99%) sepia(50%) saturate(0%) hue-rotate(91deg) brightness(111%) contrast(100%)",
                  }}
                />
              </NavLink>
            </div>
          ) : (
            <ul className="navbar-nav align-items-center">
              <li className="nav-item m-2 ">
                <a className="btn btn-danger" href="#menu">
                  Меню
                </a>
              </li>
              <li className="nav-item m-2">
                <a className="btn btn-danger" href="#about">
                  Про нас
                </a>
              </li>
              <li className="nav-item m-2">
                <div className="dropdown">
                  <button
                    className="btn btn-danger dropdown-toggle"
                    type="button"
                    data-bs-toggle="dropdown"
                  >
                    Зробити замовлення
                  </button>
                  <ul className="dropdown-menu">
                    {dropdownItems.map(({ text, path }) => (
                      <li key={text}>
                        {login ? (
                          <NavLink to={path} className="dropdown-item">
                            {text}
                          </NavLink>
                        ) : (
                          <div
                            className="dropdown-item"
                            role="button"
                            data-bs-toggle="modal"
                            data-bs-target="#modal"
                          >
                            {text}
                          </div>
                        )}
                      </li>
                    ))}
                  </ul>
                </div>
              </li>
            </ul>
          )}

          <div className="navbar-nav ms-auto align-items-center">
            <a
              href="tel:+380973536791"
              className="nav-link d-flex flex-column align-items-center link"
              aria-label="Дзвінок"
            >
              <img
                src={phoneIcon}
                alt="Phone"
                width={30}
                height={30}
                aria-hidden="true"
              />
              Дзвінок
            </a>
            {login ? (
              <div className="nav-link d-flex flex-column align-items-center">
                <img width="30" height="30" src={locationIcon} alt="Location" />
                {login}
              </div>
            ) : pathname === "/cart/" ? null : (
              <a
                href="#"
                className="nav-link d-flex flex-column align-items-center link"
                data-bs-toggle="modal"
                data-bs-target="#modal"
                aria-label="Увійти"
              >
                <img
                  src={loginIcon}
                  alt="Login"
                  width={30}
                  height={30}
                  aria-hidden="true"
                />
                Увійти
              </a>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}
