import { useList } from "react-firebase-hooks/database";
import { getDatabase, ref } from "firebase/database";
import { useState } from "react";
import { Modal as BSModal } from 'bootstrap'
import { useNavigate } from "react-router-dom";

export default function Modal({setLogin}) {
  const [input, setInput] = useState("");
  const [message, setMessage] = useState("");

  const db = getDatabase();
  const dataRef = ref(db, "logins");
  const [snapshots] = useList(dataRef);

  const inputOnChange = (e) => {
    setInput(e.target.value);
  };

  const [showMessage, setShowMessage] = useState(false);

  const nav = useNavigate();

  const btnOnClick = () => {
    if (input === "admin") {
      if (document.querySelector(".btn-close")) {
        document.querySelector(".btn-close").click();
      }
      
      nav("/admin");
    }
    snapshots.forEach(snapshot => {
      if (snapshot.val() === input) {
        setMessage("Вхід успішний")
        setLogin(input)
        setInput('')
        setShowMessage(true);
        setTimeout(() => {
          setMessage("");
          setShowMessage(false);
          
          if (document.querySelector(".btn-close")) {
            document.querySelector(".btn-close").click();
          }
        }, 2000)
      } else {
        setMessage("Неправильний логін")
        setInput('')
        setShowMessage(true);
      }
    })
  };

  return (
    <div
      className="modal fade"
      id="modal"
      tabIndex={-1}
      aria-labelledby="Login"
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered">
        <div className="modal-content">
          <div className="modal-header">
            <h1 className="modal-title fs-5"  style={{opacity: 1}}>Вхід</h1>
            <h1 className={`modal-title fs-5 ms-auto text-danger message ${showMessage ? 'show' : ''}`}>{message}</h1>
            <button
              type="button"
              className="btn-close"
              data-bs-dismiss="modal"
            />
          </div>
          <div className="modal-body">
            <input
              type="text"
              value={input}
              className="form-control"
              placeholder="Логін"
              onChange={inputOnChange}
            />
          </div>
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Закрити
            </button>
            <button type="button" className="btn btn-danger" onClick={btnOnClick}>
              Увійти
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
